import SEO from "../components/seo"
import ModalComponent from "../components/modalComponent"

//url whatsapp
const url =
  "https://api.whatsapp.com/send?phone=6285733179537&text=Assalamualaikum Es.. %0A"
const SPREADSHEET_REGIST_URL =
  "https://script.google.com/macros/s/AKfycbxCsKune1_frDfZCF_MRsgBs472uFX-BRgUY7N8CTAP6ZAAnvMfZC2WIhL-kHnOZPHZgw/exec"

class Daftar extends React.Component {
  state = {
    nama: "",
    kampus: "",
    programStudi: "",
    whatsapp: "",
    submitLoading:false,
    modal: false,
    url:'',
    //   url:  (url + 'saya ' + this.state.nama + ' dari ' + this.state.kampus + ' programStudi ' + this.state.programStudi + ' minta izin untuk daftar ke organisasi PMII Sepuluh Nopember'),
  }
  updateUrl() {
    this.state.url = url +
                    "saya " +
                    this.state.nama +
                    " dari kampus " +
                    this.state.kampus +
                    " program studi " +
                    this.state.programStudi +
                    " mengkonfirmasi untuk pendaftaran organisasi PMII Sepuluh Nopember %0ATerima kasih"
  }
  componentDidMount() {
    document.documentElement.scrollTop = 0
    document.scrollingElement.scrollTop = 0
    this.refs.main.scrollTop = 0
    this.updateUrl()
  }

  toggleModal = () => this.setState({ modal: !this.state.modal,})
  onAggreModal = () =>{
    this.toggleModal()
    setTimeout(() => {
      window.open(this.state.url, '_blank')
    }, 1000);
  } 

  onChangeInputNama = e =>{
    this.setState({nama: e.target.value})
    this.updateUrl()
  }
  onChangeInputKampus = e =>{
    this.setState({kampus: e.target.value})
    this.updateUrl()
  }
  onChangeInputProgramStudiOk = e =>{
    this.setState({programStudi: e.target.value})
    this.updateUrl()
  }
  onChangeInputWhatsapp = e =>{
    this.setState({whatsapp: e.target.value})
  }
  onSubmit = e => {
    if(!this.state.submitLoading)
    {
      this.setState({submitLoading:!this.state.submitLoading})
      e.preventDefault()
  
      fetch(SPREADSHEET_REGIST_URL, {
        method: "POST",
        body: new FormData(document.forms["form-regist"]),
      })
      .then(res => {
        this.toggleModal()
      })
      .catch(err => {
        alert(
          "Proses penyimpanan gagal, terdapat gangguan. Yuk laporkan issue ini di github kami untuk perkembangan website ini. terima kasih"
        )
      })
      .finally(() => this.setState({submitLoading:!this.state.submitLoading}))
    }
  }


  render() {
    return (
      <>
        {/* {console.log(typeof(this.state.posts[0].excerpt))} */}
        <SEO
          title="Pendaftaran"
          description="PMII Sepuluh Nopember Merupakan organisasi mahasiswa ekstra kampus (ITS, PENS, PPNS, UHT) yang menghimpun dan membina mahasiswa islam Indonesia sesuai dengan sifat dan tujuan PMII serta peraturan perundang-undangan dan paradigma PMII yang berlaku."
          lang="id"
          keywords="daftar, oprec, gabung, mapaba, join, pmii, its, ppns, pens, surabaya "
          img={require("/src/assets/img/theme/duta-pmii-1011.png")}
          // meta={[
          //   {
          //     name: "og:image",
          //     content: require("/src/assets/img/theme/duta-pmii-1011.png"),
          //   },
          //   // {
          //   //   name: 'og:url',
          //   //   content: require("/src/assets/img/theme/duta-pmii-1011.png")
          //   // },
          //   {
          //     name: "author",
          //     content: "PMII Sepuluh Nopember",
          //   },
          //   {
          //     name: "article:author",
          //     content: "https://www.facebook.com/pmii1011",
          //   },
          //   {
          //     name: "keyword",
          //     content:
          //       "daftar, oprec, gabung, mapaba, join, pmii, its, ppns, pens, surabaya ",
          //   },
          // ]}
        />
        <DemoNavbar />
        <ModalComponent
          title="Pendaftaran Berhasil"
          content="Pendaftaran anda tercatat pada sistem database kami. terima kasih. untuk tahap selanjutnya silahkan konfirmasi kepada kami"
          isOpen={this.state.modal}
          toggleModal={this.toggleModal}
          onAggreModal={this.onAggreModal}
        />
        <main ref="main">
          <section className="section section-shaped section-lg">
            <div className="shape shape-style-1 bg-gradient-default">
              <span />
              <span />
              <span />
              <span />
              <span />
              <span />
              <span />
              <span />
            </div>
            <Container className="pt-lg-7">
              <Row className="justify-content-center">
                <Col lg="5">
                  <Card className="bg-secondary shadow border-0">
                    <CardHeader className="bg-white pb-5">
                      <div className="text-muted text-center mb-3">
                        <small>Daftar menggunakan</small>
                      </div>
                      <div className="text-center">
                        <Button
                          className="btn-neutral btn-icon mr-4"
                          color="default"
                          href="#"
                          onClick={e => e.preventDefault()}
                          disabled
                        >
                          <span className="btn-inner--icon mr-1">
                            <img
                              alt="..."
                              src={require("/src/assets/img/icons/common/github.svg")}
                            />
                          </span>
                          <span className="btn-inner--text">Github</span>
                        </Button>
                        <Button
                          className="btn-neutral btn-icon ml-1"
                          color="default"
                          href="#"
                          onClick={e => e.preventDefault()}
                          disabled
                        >
                          <span className="btn-inner--icon mr-1">
                            <img
                              alt="..."
                              src={require("/src/assets/img/icons/common/google.svg")}
                            />
                          </span>
                          <span className="btn-inner--text">Google</span>
                        </Button>
                      </div>
                    </CardHeader>
                    <CardBody className="px-lg-5 py-lg-5">
                      <div className="text-center text-muted mb-4">
                        <small>Isi form pendaftaran berikut</small>
                      </div>
                      <Form
                        role="form"
                        name="form-regist"
                        onSubmit={this.onSubmit}
                      >
                        <FormGroup>
                          <InputGroup className="input-group-alternative mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="ni ni-lock-circle-open" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Nama"
                              name="nama"
                              type="text"
                              onChange={this.onChangeInputNama}
                            />
                          </InputGroup>
                        </FormGroup>
                        <FormGroup>
                          <InputGroup className="input-group-alternative mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="ni ni-hat-3" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Kampus"
                              name="kampus"
                              type="text"
                              onChange={this.onChangeInputKampus}
                            />
                          </InputGroup>
                        </FormGroup>
                        <FormGroup>
                          <InputGroup className="input-group-alternative mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="ni ni-building" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Progra Stmudi"
                              name="programStudi"
                              type="text"
                              onChange={this.onChangeInputProgramStudiOk}
                            />
                          </InputGroup>
                        </FormGroup>
                        <FormGroup>
                          <InputGroup className="input-group-alternative mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="fa fa-whatsapp" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <Input
                              placeholder="Whatsapp"
                              name="whatsapp"
                              type="text"
                              onChange={this.onChangeInputWhatsapp}
                            />
                          </InputGroup>
                        </FormGroup>
                        {/*                         
                        <Row className="my-4">
                          <Col xs="12">
                            <div className="custom-control custom-control-alternative custom-checkbox">
                              <input
                                className="custom-control-input"
                                id="customCheckRegister"
                                type="checkbox"
                              />
                              <label
                                className="custom-control-label"
                                htmlFor="customCheckRegister"
                              >
                                <span>
                                  I agree with the{" "}
                                  <a
                                    href="#pablo"
                                    onClick={e => e.preventDefault()}
                                  >
                                    Privacy Policy
                                  </a>
                                </span>
                              </label>
                            </div>
                          </Col>
                        </Row> */}
                        <div className="text-center">
                          <Button
                            className="mt-4"
                            color="primary"
                            type="submit"
                            disabled={this.state.submitLoading}
                          >
                            {this.state.submitLoading && (
                              <>
                                <span class="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></span>
                                <span class="sr-only">Loading...</span>
                              </>
                            )}
                            &nbsp; Daftar
                          </Button>

                        </div>
                      </Form>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
            </Container>
          </section>
        </main>
        <CardsFooter />
      </>
    )
  }
}

export default Daftar
